body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 97vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.jumbotron {
  margin-bottom: 0;
}

.team-comparison {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.team-container {
  width: 50%;
  min-width: 400px;
  display: -webkit-flex;
  display: flex;
  /* background-color: #222222; */
}

.split-view-stats {
  display: -webkit-flex;
  display: flex;
}

.split-view-stat {
  width: 50%;
}

.points-text {
  margin-top: 20%;
}

.team-content-container {
  width: 100%;
  max-width: 700px;
  padding: 20px;
}

.charts-and-trends-container {
  width: 100%;
  padding: 0px 20px 10px 20px;
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /* change to grid */
}

.welcome-header-container {
  padding-top: 30vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.welcome-header {
  background-color: #eeeeee;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 30px 390px 10px 390px;
}

h1 {
  font-family: 'Aldrich';
  font-style: italic;
}

.team-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
  color: #222222;
  background-color: #ffffff;
  border-bottom: 1px solid #555555;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.stat-container {
  width: 100%;
  margin-bottom: 10px;
  background-color: white;
  padding: 10px;
}

.comparison-stat-container {
  min-width: 400px;
  margin: 0 20px;
  background-color: white;
  padding: 10px;
}

.team-select-dropdown {
  width: 200px;
  color: #000000;
  text-align: left;
}

.record-text {
  font-size: 28px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.record-text-in-chart {
  position: absolute;
  padding: 75px 52px;
}

.stat-title-container {
  width: 100%;
}

.credits-and-info-container {
  background-color: #ffffff;
  border-top: 1px solid #555555;
  width: 100%;
  text-align: center;
  height: 2vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

